.selectedYear {
    margin-bottom: 10px;
}

.selectLabel {
    color: rgb(162, 158, 158);
    font-size: 16px;
    text-decoration: underline;
    margin-right: 10px;
}

.selectYear {
    padding: 5px;
    color: rgb(162, 158, 158);
    background-color: transparent;
    outline: none;
    border: 1px solid gold;
    border-radius: 5px;
    width: 130px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.searchText {
    color: rgb(162, 158, 158);
    font-size: 16px;
}